$enable-flex: true;

.indent{
  margin-left: 1rem;
}

/*! Bootstrap 4 "Executive Suite" Theme by HackerThemes, https://hackerthemes.com */

$primary: #536391;
$secondary: #918653;
$success: #539153;
$info: #6c5391;
$warning: #d39951;
$danger: #c05e4a;
$dark: #343758;
$light: #f7f3e7;

$border-radius: 0 !default;
$border-radius-sm: 0 !default;
$border-radius-lg: 0 !default;

$font-family-sans-serif: 'Source Sans Pro', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol' !default; //BS

@import "bootstrap";
$fa-font-path: '../scripts/fonts';
@import "font-awesome";
@each $color, $value in $theme-colors {
  .btn-hover-text-#{$color} {
    &:hover,
    &:active {
      color: $value !important;
    }
  }
}

.btn-wide {
  padding-left: 50px;
  padding-right: 50px;
}

.display-fix {
  margin-left: -4px;
}

.btn-pill {
  border-radius: 100px;
}

.lead-lg {
  font-size: 35px;
}

.radius-0 {
  border-radius: 0;
}

.text-spacey {
  line-height: 28px;
}

.display-1 {
  @include media-breakpoint-down(sm) {
    font-size: 4rem;
  }
}

.display-2 {
  @include media-breakpoint-down(sm) {
    font-size: 3.5rem;
  }
}

.text-oswald {
  font-family: "Oswald", sans-serif;
}

.text-merriweather {
  font-family: "Merriweather", sans-serif;
}

.jumbo-vertical-center {
  align-items: center;
  display: flex;
  flex-direction: row;
  padding-bottom: 40px;
  padding-top: 40px;
}

.lead {
  font-family: "Merriweather", sans-serif;
}

.display-1,
.display-2,
.display-3,
.display-4 {
  font-family: "Merriweather", sans-serif;
  letter-spacing: .02em;
}

.display-3 {
  font-size: 69px;

  @include media-breakpoint-down(sm) {
    font-size: 40px;
  }
}

.text-spacey {
  line-height: 26px;
}
